






















import Vue from 'vue';
import store from '../store';

interface Button {
  label: number;
  ng: boolean;
}

export default Vue.extend({
  name: 'Solve',
  data: () => ({
    buttons: [
      { label: 1, ng: false },
      { label: 2, ng: false },
      { label: 3, ng: false },
      { label: 4, ng: false },
      { label: 5, ng: false },
      { label: 6, ng: false },
      { label: 7, ng: false },
      { label: 8, ng: false },
      { label: 9, ng: false },
      { label: 10, ng: false },
    ] as Button[],
  }),

  computed: {
    category: () => store.selectedCategory,
    histories: () => store.histories,
  },

  mounted() {
    this.newQuiz();
  },

  methods: {
    showPage: (name: string) => store.showPage(name),

    initButtons() {
      if (this.category === null) { throw new Error('category is null'); }
      const over10 = (this.category.id === 'add2' ? 11 : 1);

      for (let i = 0; i < this.buttons.length; i++) {
        this.buttons[i].label = i + over10;
        this.buttons[i].ng = false;
      }
    },

    newQuiz() {
      if (this.category === null) { throw new Error('category is null'); }

      this.initButtons();

      this.category.q = this.findQuestion();
      if (this.category.q === null) {
        return;
      }

      const q = this.category.q;
      const t = q[0] + this.category.sign + q[1];
      this.histories.push({
        q,
        text: t,
        start: new Date(),
        end: null,
        ans: [],
      });
    },

    answer(btn: Button) {
      const category = this.category;
      if (category === null) { throw new Error('category is null'); }
      const q = category.q;
      if (q === null) { throw new Error('question is null'); }

      const clicked = btn.label;
      const ans = q[2];

      const h = this.histories[this.histories.length - 1];
      if (ans !== clicked) {
        h.ans.push({num: clicked, ok: false});
        btn.ng = true;
        category.isNG = true;
        setTimeout(() => {
          category.isNG = false;
        }, 500);
      } else {
        h.end = new Date();
        h.ans.push({num: ans, ok: true});
        const all = category.questions;
        all.splice(all.indexOf(q), 1);

        category.isOK = true;
        setTimeout(() => {
          category.isOK = false;
          category.done++;
          this.newQuiz();
        }, 500);
      }
    },

    findQuestion() {
      if (this.category === null) { throw new Error('category is null'); }
      const all = this.category.questions;
      if (all.length === 0) {
        return null;
      }

      return all[Math.floor(Math.random() * all.length)];
    },
  },
});
